import "@webcomponents/webcomponentsjs/webcomponents-lite";
import {RouterHandler} from "./router/router-handler";
import {Authentication} from "./auth/authentication";
import {Core} from "./core/core";
import {Http} from "./http/http";

/**
 * Order is important !
 */
class App {
    constructor() {
        new Authentication();
        const router = new RouterHandler();
        new Core();
        new Http();
        router.init();
    }
}

if ('registerElement' in document
    && 'import' in document.createElement('link')
    && 'content' in document.createElement('template')) {
    // platform is good!
    new App();
} else {
    setTimeout(() => {
        new App();
    });
}

window.onload = (event) => {
    console.log('page is fully loaded');console.log(window.user);
    Authentication.instance.doAuthentication("", "", window.user)
    window.addEventListener("message", (event) => {console.log(event.origin,'msg');
        if (event.origin !== "http://localhoat:8000")
            return;

        console.log(event.data);
        //Authentication.instance.doAuthentication("", "", window.user).then(success => {})

        //let user = event.data;
    }, false);
};
